<template>
    <div id="app">
        <Fheader v-bind:title="header_title"></Fheader>
        <div class="article-container">
            <p class="article-page-title">{{ article_data.article.title }}</p>
            <div class="article-page-tags">
                <template v-for="(tag, index) in article_data.article.labels">
                    <div class="article-page-tag">
                        <i class="icon-tag"></i>
                        <span>{{ tag }}</span>
                    </div>
                </template>
            </div>
            <div class="article-content" v-html="article_data.article.content"></div>
        </div>
        <template v-if="article_data.article.allow_comment == 0">
            <div class="comments-container">
                <div class="comments-bar">
                    <div class="comments-area">
                        <div class="thumbs-up">
                            <i class="icon-thumbs-up" v-on:click="thumbsUpHandler"></i>
                            <span>加油:{ article_data.article.cheer_number }}</span>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <template v-else>
            <div class="comments-container">
                <div class="comments-bar">
                    <div class="comments-bar-tip">参与讨论</div>
                    <div class="comments-area">
                        <div class="thumbs-up">
                            <i class="icon-thumbs-up" v-on:click="thumbsUpHandler"></i>
                            <span>加油:{{ article_data.article.cheer_number }}</span>
                        </div>
                        <div class="comments-count">
                            <i class="icon-comments"></i>
                            <span>{{ article_data.article.comment_number }}条评论</span>
                        </div>
                    </div>
                </div>
                <div class="comments-form">
                    <textarea class="textarea-default" v-bind:placeholder="placeholder" v-model="comment_content"></textarea>
                    <div class="comments-btns">
                        <button class="button-default button-comment" v-on:click="commentHandler">提交评论</button>
                    </div>
                </div>
                <div class="comments-bar">
                    <div class="comments-bar-tip">评论</div>
                </div>
                <div class="comments-list">
                    <template v-for="(comment, index) in article_data.article.comments">
                        <div class="comments-item">
                            <div class="comments-avar">
                                <img src="../../assets/images/data_btn@2x.png">
                            </div>
                            <div class="comments-detail">
                                <div class="comments-title-area">
                                    <div class="comments-title">{{ comment.nickname }}</div>
                                    <div class="comments-reply" v-on:click="replyHandler(comment.comment_id, comment.nickname)">回复</div>
                                </div>
                                <div class="comments-content">
                                    <span v-show="comment.response_nickname != ''">回复 <span class="response_to">{{ comment.response_nickname }}</span> ：</span>
                                    {{ comment.content }}
                                </div>
                            </div>
                        </div>
                    </template>
                </div>
            </div>
        </template>
		<transition name="fade">
			<Toast v-bind:msg="toast_text" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>
<script>
import Fheader from '../../components/common/Fheader'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toast_text: '',
            header_title: '文章详情',
            nextlink: 'http://www.baidu.com',
            article_id: 0,
            comment_to_id: '',
            comment_to_nickanme: '',
            comment_content: '',
            placeholder: '评论文章...',
            article_data: {
                has_login: "0/1",  //是否已登陆
                nickname : "",  //如果登录了，返回此参数
                avatar : "",
                has_cheered: "",  //如果登陆过，返回该参数，用户是否加油过 0 否 1 是
                can_comment: 0,  //如果登陆过，返回该参数，用户是否可以评论 0 否 1 是
                article: {
                    title: "文章标题文章标题文章标题文章标题",  //标题
                    labels: ['aa', 'bb'],
                    release_time: "2016-03-01 10:20:20", //发布时间 2016-03-01 10:20:20
                    content: "文章内容文章内容文章内容文章内容文章内容",  //正文内容，html内容
                    cheer_number: 6,  //加油数量
                    allow_comment: 1,  // 0 不允许评论  1 允许
                    comment_number: 10, //评论数量
                    comments: [
                        {
                            comment_id: 1, //comment主键，评论时用
                            comment_response_id: "",  //comment response 主键， comment_id和comment_response_id两个参数是或的关系，每个model只有一种
                            content: "如果为回复某人的，回复昵称如果为回复某人的，回复昵称",  //回复内容
                            nickname: "评论昵称", //评论人昵称
                            avatar: "",  //评论人头像 
                            response_nickname: "王小林"  //如果为回复某人的，回复昵称
                        }
                    ]
                }
            }
    	}
    },
    mounted: function(){
        var that = this;
        var request_para = utils.get_request();
        if(!request_para.id){
            that.toast_text = '文章id为空！';
            that.showToast();
            return ;
        }
        that.article_id = request_para.id;
        store.init_article(that.article_id).then(function(data){
            console.log(data);
            /*if(data.data.code == 0){
                that.article_data = data.data.data;
            }else{
                that.toast_text = data.data.message;
                that.showToast();
            }*/
        }).catch(function(error){
            console.log(error);
        });
    },
    methods:{
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 1500);
    	},
        thumbsUpHandler: function(){
            var that = this;
            store.thumb_up(that.article_id).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    that.article_data.article.cheer_number++;
                    that.toast_text = data.data.message;
                    that.showToast();
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
        },
        commentHandler: function(){
            var that = this;
            store.article_comment(that.article_id, that.comment_to_id, that.comment_to_nickanme, that.comment_content).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    var comment_data = {
                        
                    };
                    that.toast_text = data.data.message;
                    that.showToast();
                    that.resetCommentForm();
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
        },
        resetCommentForm(){
            var that = this;
            that.comment_content = '';
            that.comment_to_id = '';
            that.comment_to_nickanme = '';
        },
        replyHandler: function(comment_id, nickname){
            var that = this;
            that.response_to_id = comment_id;
            that.response_to_nickname = nickname;
            that.placeholder = '回复给 '+nickname+' ：';
        },
        nextHandler: function(){
            var that = this;
            var data = that.getFormData();
            
        },
        getFormData: function(){
            //dealData处理成form数据，data对象key对应后端接口
            var that = this;
            var data = {};

            return data;
        }
    },
    components: {
    	Fheader,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
@import '../../assets/css/detail.css';
.doctor-form{
    padding: 10px 15px 60px 15px;
}
</style>



